'use strict'; // MODULES //

var hasOwnProp = require('@stdlib/assert/has-own-property');

var isEnumerableProperty = require('@stdlib/assert/is-enumerable-property');

var isArray = require('@stdlib/assert/is-array');

var isInteger = require('@stdlib/math/base/assert/is-integer');

var MAX_LENGTH = require('@stdlib/constants/math/uint32-max'); // MAIN //

/**
* Tests whether a value is an `arguments` object.
*
* @private
* @param {*} value - value to test
* @returns {boolean} boolean indicating whether a value is an `arguments` object
*
* @example
* function foo() {
*     return arguments;
* }
*
* var bool = isArguments( foo() );
* // returns true
*
* @example
* var bool = isArguments( [] );
* // returns false
*/


function isArguments(value) {
  return value !== null && typeof value === 'object' && !isArray(value) && typeof value.length === 'number' && isInteger(value.length) && value.length >= 0 && value.length <= MAX_LENGTH && hasOwnProp(value, 'callee') && !isEnumerableProperty(value, 'callee');
} // EXPORTS //


module.exports = isArguments;