'use strict'; // MODULES //

var dtypes = require('@stdlib/ndarray/dtypes');

var KEY_OPTION_VALUES = require('./key_option_values.json'); // VARIABLES //


var DTYPES = dtypes();
var len = DTYPES.length;
var N = KEY_OPTION_VALUES.length; // MAIN //

/**
* Returns a constructor cache.
*
* ## Notes
*
* -   Should return something similar to the following:
*
*     ```text
*     {
*         "float64;codegen=true": [],
*         "float64;codegen=false": [],
*         "float32;codegen=true": [],
*         "float32;codegen=false": [],
*         ...
*     }
*     ```
*
* @private
* @returns {Object} constructor cache
*/

function cache() {
  var out;
  var key;
  var dt;
  var i;
  var j;
  out = {};

  for (i = 0; i < len; i++) {
    dt = DTYPES[i];

    for (j = 0; j < N; j++) {
      key = dt + ';' + KEY_OPTION_VALUES[j];
      out[key] = []; // Note: we use an array to avoid unordered key look-up
    }
  }

  return out;
} // EXPORTS //


module.exports = cache;