'use strict'; // MODULES //

var isEnumerableProperty = require('@stdlib/assert/is-enumerable-property'); // VARIABLES //


var obj = {
  'toString': null
}; // MAIN //
// Note: certain environments don't allow enumeration of overwritten properties which are considered non-enumerable...

var bool = !isEnumerableProperty(obj, 'toString'); // EXPORTS //

module.exports = bool;