'use strict'; // MAIN //

/**
* Returns the global object using code generation.
*
* @private
* @returns {Object} global object
*/

function getGlobal() {
  return new Function('return this;')(); // eslint-disable-line no-new-func
} // EXPORTS //


module.exports = getGlobal;