'use strict'; // MODULES //

var isArguments = require('./main.js'); // VARIABLES //


var bool; // FUNCTIONS //

/**
* Detects whether an environment returns the expected internal class of the `arguments` object.
*
* @private
* @returns {boolean} boolean indicating whether an environment behaves as expected
*
* @example
* var bool = detect();
* // returns <boolean>
*/

function detect() {
  return isArguments(arguments);
} // MAIN //


bool = detect(); // EXPORTS //

module.exports = bool;