'use strict';
/**
* Creates a function which always returns the same value.
*
* @param {*} [value] - value to always return
* @returns {Function} constant function
*
* @example
* var fcn = wrap( 3.14 );
*
* var v = fcn();
* // returns 3.14
*
* v = fcn();
* // returns 3.14
*
* v = fcn();
* // returns 3.14
*/

function wrap(value) {
  return constantFunction;
  /**
  * Constant function.
  *
  * @private
  * @returns {*} constant value
  */

  function constantFunction() {
    return value;
  }
} // EXPORTS //


module.exports = wrap;